"use client";
import * as React from "react";
import { Terminal } from "@/components/system/Terminal";
import { SystemCanvas } from "@/components/system/SystemCanvas";

export default function Page() {
  const [unlocked, setUnlocked] = React.useState(false);

  return (
    <main className="h-screen w-screen bg-[#050505] overflow-hidden cursor-crosshair">
      {!unlocked ? (
        <div className="flex h-full items-center justify-center">
          <Terminal onAccess={() => setUnlocked(true)} />
        </div>
      ) : (
        <SystemCanvas />
      )}
    </main>
  );
}