"use client";
import * as React from "react";

export function Terminal({ onAccess }: { onAccess: () => void }) {
  const [val, setVal] = React.useState("");
  const [status, setStatus] = React.useState("WAITING_FOR_INPUT");

  const handleInput = (e: React.KeyboardEvent) => {
    if (e.key === "Enter") {
      if (val.toLowerCase().includes("build")) {
        setStatus("INITIALIZING_EXPERIENCE...");
        setTimeout(onAccess, 1000);
      } else {
        setStatus("REJECTED: INPUT_TOO_GENERIC");
        setVal("");
      }
    }
  };

  return (
    <div className="font-mono text-[11px] uppercase tracking-widest text-zinc-600">
      <div className="mb-4">{status}</div>
      <div className="flex gap-2">
        <span className="text-zinc-400">{">"}</span>
        <input 
          autoFocus
          className="bg-transparent outline-none text-zinc-200 w-64"
          placeholder="WHAT DO YOU WANT TO MAKE?"
          value={val}
          onChange={(e) => setVal(e.target.value)}
          onKeyDown={handleInput}
        />
      </div>
      <div className="mt-20 opacity-20 text-[9px]">
        Hint: Don't ask for a portfolio. Ask to build something.
      </div>
    </div>
  );
}