"use client";
import { motion } from "framer-motion";

export function SystemCanvas() {
  return (
    <div className="relative h-full w-full p-10">
      {/* This is a 'draggable' artifact representing a project */}
      <DraggableItem title="Project_01.exe" color="blue" x={100} y={150} />
      <DraggableItem title="Raw_Experiment.mov" color="purple" x={400} y={100} />
      <DraggableItem title="The_Mental_Model.pdf" color="white" x={200} y={400} />
      
      <div className="absolute bottom-10 left-10 font-mono text-[10px] text-zinc-500">
        SYSTEM_ACTIVE // STABLE_ENVIRONMENT
      </div>
    </div>
  );
}

function DraggableItem({ title, color, x, y }: any) {
  return (
    <motion.div 
      drag
      dragMomentum={false}
      initial={{ x, y }}
      className="absolute cursor-grab active:cursor-grabbing group"
    >
      <div className="h-12 w-12 border border-white/10 bg-white/5 group-hover:bg-white/10 transition-colors flex items-center justify-center">
        <div className={`h-2 w-2 rounded-full bg-${color}-500 shadow-[0_0_10px_rgba(255,255,255,0.5)]`} />
      </div>
      <span className="mt-2 block text-[9px] font-mono text-zinc-500">{title}</span>
    </motion.div>
  );
}